import React from 'react';
import { navigate } from 'gatsby';

import * as styles from './Brand.module.scss';



const Brand = () => {
  return (
    <div
      className={styles.root}
      role={'presentation'}
      onClick={() => navigate('/')}
    >
      <img src="/brand.png" className={styles.brandImg} alt="" />
    </div>
  );
};

export default Brand;
